import "./styles.css";
import React from "react";

export default function App() {
  const [{ useState }] = [React];
  const App = () => {
    const s = useState({ r: 100, g: 255, b: 200 });
    return (
      <div style={{ background: `rgb(${s[0].r}, ${s[0].g}, ${s[0].b})` }}>
        <Compo s={s} col="r" />
        <Compo s={s} col="g" />
        <Compo s={s} col="b" />
      </div>
    );
  };
  const Compo = ({ s, col }) => {
    const [state, SetState] = s;
    const f = ({ target: { value } }) =>
      SetState((o) => ({ ...o, [col]: value }));
    return (
      <div>
        <input
          value={state[col]}
          onChange={f}
          type="number"
          min="0"
          max="255"
        />
        <input
          onChange={f}
          type="range"
          min="0"
          max="255"
          step="1"
          value={state[col]}
        />
      </div>
    );
  };
  return (
    <div className="App">
      <h1>Color Mixer</h1>
      <div>
        <App />
      </div>
    </div>
  );
}
